<script>
import Info from "@/components/Info.vue";
import Nav from "@/components/Nav.vue";
import { axios, realUrl } from "@/service/api.js";
import { JX3BOX, User } from "@jx3box/jx3box-common";
// import Extend from "@/components/Extend.vue";

export default {
    name: "Icons",
    data: function() {
        return {
            activeTabName: "icon",
            searchIconInput: "",
            iconsList: [],
            clickedIndex: -1,
            IconPath: JX3BOX.__iconPath + "icon/",
            EmojiPath: JX3BOX.__ossMirror + "emotion/official_mini/",
            faviconsList: null,
            localFaviconsList: [],
            isSynchronizing: false,
            total: {},
            emojiCategoryOptions: [],
            emojiSelection: 0,
            isDownloadingEmoji: false,
            isSearchingByName: false,
        };
    },
    computed: {
        faviconNeedsSync() {
            if (
                !this.localFaviconsList ||
                !this.faviconsList ||
                this.localFaviconsList.length === 0
            ) {
                return false;
            }
            if (this.localFaviconsList.length !== this.faviconsList.length) {
                return true;
            }
            for (let each of this.localFaviconsList) {
                if (!this.faviconsList.includes(each)) {
                    return true;
                }
            }
            return false;
        },
    },
    methods: {
        async prepareMounted() {
            let defaultList = [
                13,
                316,
                109,
                245,
                889,
                2178,
                2179,
                2180,
                2588,
                2589,
                2646,
                2647,
                2648,
                2789,
                3089,
                3111,
                3112,
                3113,
                3114,
                3115,
                3116,
                3117,
                3118,
                3119,
                3120,
                3121,
                3122,
                3123,
                3137,
                3138,
                3139,
                3140,
                3321,
                4704,
                4707,
                4708,
                4709,
                4710,
                4835,
                4853,
                5389,
                8848,
                10451,
                10452,
                10909,
            ];
            this.iconsList = defaultList;

            // ËøôÈáåÂÖàËØªÂèñ‰∏ÄÊ¨°Â≠òÂú®Êú¨Âú∞ÁöÑÊî∂ËóèÂõæÊ†á
            this.getFromLocal();

            let allIconsListUrl =
                JX3BOX.__staticPath.jsdelivr + "jx3-icon@1.1.0/icon.json";
            let getAllIconsList = axios(allIconsListUrl, "GET");

            let allEmojiListUrl =
                JX3BOX.__staticPath.jsdelivr + "jx3-icon@1.1.0/emotion.json";
            let getAllEmojiList = axios(allEmojiListUrl, "GET");

            let getUserServerSaved = null;
            if (this.uid) {
                getUserServerSaved = this.getSavedIcons();
            } else {
                getUserServerSaved = this.getFromLocal();
            }
            let axiosSuccess = false;
            Promise.all([getAllIconsList, getAllEmojiList])
                .then((result) => {
                    this.total.icons = result[0].icon;
                    this.emojiCategoryOptions = result[1];
                })
                .catch((e) => {
                    switch (e.code) {
                        case -1:
                            // ÁΩëÁªúÂºÇÂ∏∏
                            this.$message.error(e.msg);
                            break;
                        case 9999:
                            this.$message.error("ÁôªÂΩïÂ§±Êïà, ËØ∑ÈáçÊñ∞ÁôªÂΩï");
                            User.destroy();
                            setTimeout(() => {
                                User.toLogin();
                            }, 2000);
                            break;
                        default:
                            // ÊúçÂä°Âô®ÈîôËØØ
                            console.log(e);
                            this.$message.error(`[${e.code}]${e.msg}`);
                    }
                });
        },
        syncFavicon() {
            this.isSynchronizing = true;
            //ÂàõÂª∫‰∏Ä‰∏™Âπ∂ÈõÜ
            let union = this.faviconsList.concat(this.localFaviconsList);
            union = new Set(union);
            this.faviconsList = Array.from(union);
            this.setSavedIcons();
            if (window.localStorage) {
                localStorage.removeItem("favicons");
                this.localFaviconsList = null;
            }
        },
        handleDownloadEmoji() {
            this.isDownloadingEmoji = true;
            let link = document.createElement("a");
            link.href = `${this.EmojiPath}${
                this.emojiCategoryOptions[this.emojiSelection].name
            }.zip`;
            link.download = `${
                this.emojiCategoryOptions[this.emojiSelection].name
            }.zip`;
            link.click();
            this.isDownloadingEmoji = false;
        },
        handleSelectEmojiCategory(index) {
            this.emojiSelection = index;
        },
        handleClick(tab, event) {
            // console.log(tab)
        },
        handleAddFavorite(index, iconid) {
            this.clickedIndex = index;
            let id = iconid + "";
            if (this.faviconsList.includes(id)) {
                return;
            }
            this.faviconsList.push(id);
            this.setSavedIcons();
        },
        handleRemoveFavorite(index, iconid) {
            let iconidIndex = this.faviconsList.indexOf(iconid);
            if (iconidIndex !== -1) {
                this.faviconsList.splice(iconidIndex, 1);
                this.setSavedIcons();
            }
        },
        handleMouseLeaveIcon() {
            this.clickedIndex = -1;
        },
        async handleSearchIcon() {
            // ÂàùÂßãÂåñÂèòÈáè
            //ÊØèÊ¨°Âä†ËΩΩÈôêÂà∂
            let limit = 500;
            let min = 0;
            let max = 1;
            let range = [];

            let query = this.searchIconInput.replace(/\ /g, "");
            if (query === "") {
                return;
            }
            let searchList = [];
            let tmpList = [];
            //Â¶ÇÊûúÂá∫Áé∞ÂÖ®ËßíÈÄóÂè∑„ÄÅÈ°øÂè∑„ÄÅÊñúÊù†„ÄÅÈ£òÈîÆËøõË°åÊõøÊç¢
            query = query.replace(/Ôºå|„ÄÅ|\/|\||\\/g, ",");
            query = query.replace(/~/g, "-");

            // Â¶ÇÊûúÊ≤°ÊúâÂàÜÈöîÁ¨¶ÔºåÂÖàÂà§Êñ≠ÊòØ‰∏çÊòØÊåâÁÖßÂêçÂ≠óÊêúÁ¥¢ÁöÑÊñáÂ≠ó
            let numberReg = /^[0-9]+$/;
            if (
                !query.includes(",") &&
                !query.includes("-") &&
                !numberReg.test(query)
            ) {
                // ÊåâÁÖßÂêçÁß∞ÊêúÁ¥¢
                this.isSearchingByName = true;
                let results = await this.searchIconByName(query);
                this.isSearchingByName = false;
                return;
            }

            // Â¶ÇÊûúÂêåÊó∂Âá∫Áé∞ÈÄóÂè∑ÂíåÊù†ÔºåÂÖàÊãÜÈÄóÂè∑ÔºåÂÜçÊãÜÊù†
            let bothExist = query.includes(",") && query.includes("-");
            if (query.includes(",")) {
                tmpList = query.split(",");
            }
            if (tmpList.length === 0) {
                tmpList = [query];
            }
            tmpList.forEach((value) => {
                if (value.includes("-")) {
                    range = value.split("-");
                    min = parseInt(range[0]);
                    max = parseInt(range[range.length - 1]);
                    if (!isNaN(min) && !isNaN(max)) {
                        if (min > max) {
                            for (let i = max; i <= min; ++i) {
                                if (!searchList.includes(i)) {
                                    searchList.push(i);
                                }
                            }
                        } else {
                            for (let i = min; i <= max; ++i) {
                                if (!searchList.includes(i)) {
                                    searchList.push(i);
                                }
                            }
                        }
                    }
                } else {
                    if (!isNaN(parseInt(value))) {
                        searchList.push(parseInt(value));
                    }
                }
            });

            this.iconsList = searchList.slice(0, 500);
        },
        async searchIconByName(name) {
            let url = JX3BOX.__node + "icon/name/" + name;
            return axios(url, "GET")
                .then((response) => {
                    if (response) {
                        let tmpList = [];
                        for (let key in response) {
                            response[key].forEach((item) => {
                                let iconid = item.iconID + "";
                                if (!tmpList.includes(iconid)) {
                                    tmpList.push(iconid);
                                }
                            });
                        }
                        this.iconsList = tmpList;
                    }
                })
                .catch((e) => {
                    switch (e.code) {
                        case -1:
                            // ÁΩëÁªúÂºÇÂ∏∏
                            this.$message.error(e.msg);
                            this.getFromLocal();
                            break;
                        default:
                            // ÊúçÂä°Âô®ÈîôËØØ
                            this.$message.error(`[${e.code}]${e.msg}`);
                            this.getFromLocal();
                    }
                });
        },

        // ÂàÜÂâ≤Á∫ø
        getUserId() {
            if (User.isLogin()) {
                this.uid = User.getInfo().uid;
            }
        },
        getSavedIcons() {
            // Ëé∑ÂèñÁî®Êà∑ÂÇ®Â≠òÁöÑÊúçÂä°Âô®ÂàóË°®
            let url = realUrl(JX3BOX.__server, "user/meta");
            if (this.uid) {
                // ‰ªéÊúçÂä°Âô®ËØªÂèñ
                // ÊóßÁâàÊï∞ÊçÆÊ†ºÂºè ‚Äô["109","3118","3119","13","316","2179","245","889","2178","5389"]‚Äò
                axios(
                    url,
                    "GET",
                    true,
                    {},
                    {},
                    { uid: this.uid, key: "favicons" }
                )
                    .then((response) => {
                        if (response.code == 0) {
                            let serverValue = response.data.value;
                            if (serverValue) {
                                // Âà§Êñ≠ÊòØÂê¶ÊòØÊóßÁâàÊï∞ÊçÆ
                                // like -> '["345", "332", "  303"]'
                                if (serverValue.includes("[")) {
                                    serverValue = serverValue.replace(/[\[\]"\ ]/g,'')
                                }
                                // // Âà§Êñ≠ÊòØÂê¶ÊòØÊóßÁâàÊï∞ÊçÆ
                                // if (serverValue.includes("[")) {
                                //     this.faviconsList = JSON.parse(serverValue);
                                // } else {
                                //     this.faviconsList = serverValue.split(",");
                                // }
                                this.faviconsList = serverValue.split(",");
                            } else {
                                this.faviconsList = [];
                            }
                        }
                    })
                    .catch((e) => {
                        switch (e.code) {
                            case -1:
                                // ÁΩëÁªúÂºÇÂ∏∏
                                this.$message.error(e.msg);
                                this.faviconsList = this.localFaviconsList;
                                break;
                            case 9999:
                                this.$message.error("ÁôªÂΩïÂ§±Êïà, ËØ∑ÈáçÊñ∞ÁôªÂΩï");
                                User.destroy();
                                setTimeout(() => {
                                    User.toLogin();
                                }, 2000);
                                //‰∏çÊåáÂÆöurlÊó∂ÂàôËá™Âä®Ë∑≥ÂõûÂΩìÂâçÊâÄÂú®È°µÈù¢
                                break;
                            default:
                                // ÊúçÂä°Âô®ÈîôËØØ
                                this.$message.error(`[${e.code}]${e.msg}`);
                                this.faviconsList = this.localFaviconsList;
                        }
                    })
                    .then(() => {});
            } else {
                // Êú¨Âú∞ËØªÂèñ
                this.faviconsList = this.localFaviconsList;
            }
        },
        getFromLocal() {
            if (window.localStorage) {
                let current = localStorage.getItem("favicons");
                if (current) {
                    // Âà§Êñ≠ÊòØÂê¶ÊòØÊóßÁâàÊï∞ÊçÆ
                    // like -> '["345", "332", "  303"]'
                    if (current.includes("[")) {
                        current = current.replace(/[\[\]"\ ]/g,'')
                    }
                    this.localFaviconsList = current.split(",");
                }
            }
        },
        setSavedIcons() {
            if (this.uid) {
                // ‰øùÂ≠òÂà∞ÊúçÂä°Âô®
                let url = realUrl(JX3BOX.__server, "user/meta");
                axios(url, "POST", true, {
                    uid: this.uid,
                    key: "favicons",
                    value: this.faviconsList.join(","),
                })
                    .then((response) => {
                        if (response.code == 0) {
                            //
                        }
                    })
                    .catch((e) => {
                        // Â¶ÇÊûúÂá∫ÈóÆÈ¢òÔºåÂÖàÂ≠òÊú¨Âú∞
                        //ÂàõÂª∫‰∏Ä‰∏™Âπ∂ÈõÜ
                        let union = this.faviconsList;
                        if (
                            this.localFaviconsList &&
                            this.localFaviconsList.length > 0
                        ) {
                            union = this.localFaviconsList.concat(
                                this.faviconsList
                            );
                        }
                        union = new Set(union);
                        this.faviconsList = Array.from(union);
                        this.setToLocal();
                        switch (e.code) {
                            case -1:
                                // ÁΩëÁªúÂºÇÂ∏∏
                                this.$message.error(e.msg);
                                break;
                            case 9999:
                                this.$message.error("ÁôªÂΩïÂ§±Êïà, ËØ∑ÈáçÊñ∞ÁôªÂΩï");
                                User.destroy();
                                setTimeout(() => {
                                    User.toLogin();
                                }, 2000);
                                //‰∏çÊåáÂÆöurlÊó∂ÂàôËá™Âä®Ë∑≥ÂõûÂΩìÂâçÊâÄÂú®È°µÈù¢
                                break;
                            default:
                                // ÊúçÂä°Âô®ÈîôËØØ
                                this.$message.error(`[${e.code}]${e.msg}`);
                        }
                    })
                    .then(() => {
                        this.isSynchronizing = false;
                    });
            } else {
                // ÂÇ®Â≠òÂú®Êú¨Âú∞
                this.setToLocal();
                this.isSynchronizing = false;
            }
        },
        setToLocal() {
            if (window.localStorage) {
                let names = this.faviconsList.join(",");
                localStorage.setItem("favicons", names);
                this.localFaviconsList = this.faviconsList;
            }
        },
        onCopy: function(val) {
            this.$notify({
                title: "Â§çÂà∂ÊàêÂäü",
                message: "Â§çÂà∂ÂÜÖÂÆπ : " + val.text,
                type: "success",
            });
        },
        onError: function() {
            this.$notify.error({
                title: "Â§çÂà∂Â§±Ë¥•",
                message: "ËØ∑ÊâãÂä®Â§çÂà∂",
            });
        },
    },
    filters: {},
    mounted: function() {
        this.getUserId();
        this.prepareMounted();
        this.getSavedIcons();
        // this.loadAllServers();
    },
    components: {
        Nav,
        // Extend,
    },
};
</script>

